.adaptive-runner {
  width: 0; //    js,   
  height: 0; //    js
  position: absolute;
  top: 0;
  left: 0;
  transition: transform 0.3s ease;
  pointer-events: none;
  z-index: 3;
}

.adaptive-runner__inner {
  width: 0; //    js,  
  height: 0; //    js,  
  position: absolute;
  top: 0;
  left: 0;
  background-color: #2c39f2;
  transition: width 0.3s ease, height 0.3s ease;
}

