.tabs--runner {
  & > .tabs__controls {
    background-color: rgba(44, 57, 242, 0.1);
    overflow: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
    align-items: center;

    &::-webkit-scrollbar {
      display: none;
    }

    @media screen and (max-width: 767px) {
      padding-bottom: 10px;
      height: 148px;
    }

    .tabs__control {
      color: black;
      height: 60px;
      flex-shrink: 0;
      opacity: 1;
      background: none;
      z-index: 4;

      &.is-active {
        color: white;
      }

      @media (hover: hover) {
        &:hover:not(.focus-visible) {
          background-color: rgba(44, 57, 242, 0.1);
        }
      }

      &.focus-visible:focus {
        background-color: rgba(44, 57, 242, 0.1);
      }
    }
  }
}
